/* Estilos generales */
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #f0f0f5;
  color: #333;
  scroll-behavior: smooth;
  transition: background-color 0.3s ease;
}

h1, h2, h3 {
  font-weight: 600;
  color: #1d1e22;
  margin-bottom: 20px;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 1.8rem;
}

/* Header */
.header {
  background: linear-gradient(135deg, #1d1e22, #4b79a1);
  color: #fff;
  padding: 100px 20px;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.header:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url('https://www.transparenttextures.com/patterns/triangles.png');
  opacity: 0.1;
}

.header h1 {
  font-size: 4rem;
  letter-spacing: 2px;
  animation: slideIn 1s ease-out;
}

.header p {
  font-size: 1.5rem;
  margin-bottom: 30px;
}

@keyframes slideIn {
  from {
    transform: translateY(-50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.btn {
  background-color: #61dafb;
  color: white;
  padding: 15px 30px;
  border-radius: 50px;
  font-size: 1.2rem;
  text-decoration: none;
  transition: all 0.4s ease;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}

.btn:hover {
  background-color: #21a1f1;
  box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.2);
  transform: translateY(-3px);
}

/* Secciones generales */
section {
  padding: 80px 20px;
  text-align: center;
  background-color: #ffffff;
  margin: 30px 0;
  border-radius: 15px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, transform 0.3s ease;
}

section:hover {
  background-color: #f5f5f5;
  transform: translateY(-5px);
}

.project-list, .experience-list, .testimonial-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-top: 40px;
}

/* Proyectos */
.project {
  background-color: #f9f9f9;
  padding: 30px;
  border-radius: 15px;
  transition: all 0.3s ease;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
}

.project h3 {
  margin-bottom: 15px;
}

.project a {
  color: #61dafb;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
}

.project a:hover {
  color: #21a1f1;
}

.project:hover {
  transform: translateY(-5px);
  background-color: #fafafa;
  box-shadow: 0px 15px 25px rgba(0, 0, 0, 0.1);
}

/* Experiencia */
.experience-list .job {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 15px;
  border-left: 5px solid #61dafb;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
}

.experience-list .job:hover {
  border-left: 5px solid #21a1f1;
}

/* Habilidades */
.skills ul {
  list-style-type: none;
  padding: 0;
}

.skills ul li {
  font-size: 1.2rem;
  background-color: #61dafb;
  color: #fff;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  display: inline-block;
  transition: background-color 0.3s ease;
}

.skills ul li:hover {
  background-color: #21a1f1;
}

/* Testimonios */
.testimonials {
  background-color: #f0f7fa;
}

.testimonials .testimonial {
  padding: 20px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
}

.testimonials .testimonial:hover {
  transform: translateY(-10px);
}

.testimonials p {
  font-style: italic;
  color: #555;
}

.testimonials h4 {
  margin-top: 15px;
  font-weight: bold;
  color: #333;
}

/* Contacto */
.contact form {
  display: grid;
  gap: 15px;
  max-width: 600px;
  margin: 0 auto;
}

.contact form label {
  font-size: 1.1rem;
  font-weight: bold;
}

.contact form input,
.contact form textarea {
  padding: 15px;
  border-radius: 10px;
  border: 1px solid #ccc;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.contact form input:focus,
.contact form textarea:focus {
  border-color: #61dafb;
}

.contact form button {
  background-color: #61dafb;
  color: white;
  padding: 15px;
  border: none;
  border-radius: 50px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.contact form button:hover {
  background-color: #21a1f1;
  transform: translateY(-3px);
}

/* Footer */
footer {
  background-color: #1d1e22;
  color: white;
  padding: 30px 0;
  text-align: center;
}

.footer p {
  margin: 0;
}

.social-links a {
  color: white;
  margin: 0 15px;
  font-size: 1.2rem;
  transition: color 0.3s ease;
}

.social-links a:hover {
  color: #61dafb;
}

/* Animaciones */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.project, .testimonial, .job {
  animation: fadeIn 0.5s ease-out;
}



.carousel-container {
  margin: 50px auto;
  width: 80%;
  max-width: 1000px;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

.carousel .legend {
  background: rgba(0, 0, 0, 0.7);
  padding: 10px;
  border-radius: 10px;
  bottom: 30px;
  font-size: 1.2rem;
}

.carousel .slide img {
  border-radius: 15px;
}

.carousel .control-arrow {
  background: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
}

.carousel .control-arrow:hover {
  background: rgba(0, 0, 0, 0.8);
}
